<template>
  <!--<modal :value="show" effect="zoom" width="350px" :backdrop="true" v-on:cancel="onCancel" style="z-index: 2000;">
  </modal>-->
  <modal v-model="show" @hidden="onCancel" size="md" style="max-width: 200px;">
    <div slot="modal-header" class="modal-header text-center" style="margin: 0 auto;">
      <p class="navbar-brand" style="color: #fff; margin-bottom: 0; padding-bottom: 0;">
        <span><img src="../../../static/bb-lg.svg" style="margin:auto;" height="43"/></span>
        <span style="vertical-align: top;position:relative; top:-3px;">
            <i>
              <span>Best</span>
              <span style="color:#03a9f4;">Bargains</span>
            </i>
      <p style="margin:0; padding:0; font-size: 11px; position: absolute; right:0; bottom: -10px;">BETA</p>
      </span>
      </p>
      <button @click="onCancel" class="btn"
              style="position:absolute; background-color: transparent !important; color: white; margin:0; padding:0; right: -15px; top: 0px">
        <i class="fa fa-times" style="font-size:1.2em" aria-hidden="true"></i></button>
    </div>

    <div class="modal-body login">
      <!--<tabs v-model="activeTab" :nav-style="navStyle" :justified="justified">
        <tab header="Login">
            <div class="btn-group">
              <button type="button" class="btn btn-raised btn-success">Login</button>
              <button type="button" class="btn btn-raised btn-success">Register</button>
            </div>
        </tab>
        <tab header="Register">
          <p>2</p>
        </tab>
      </tabs>-->
      <ul class="nav nav-tabs nav-justified" role="tablist">
        <li class="nav-item" style="padding:0; margin-left:0;">
          <a class="nav-link active" @click="setTab('login')" style="padding:0;" data-animation="fade in"
             data-toggle="tab" href="#login" role="tab">Login</a>
        </li>
        <li class="nav-item" style="padding:0; margin-left:0;">
          <a class="nav-link" @click="setTab('register')" data-toggle="tab" href="#register" role="tab">Register</a>
        </li>
      </ul>
      <div class="text-center social-btn" style="vertical-align:center; margin:auto; margin-top:15px;">
        <a class="btn btn-social-icon btn-google" @click="signInWithGoogle" style="color:white;"><span
          class="fa fa-google"></span></a>
        <a class="btn btn-social-icon btn-twitter" @click="signInWithTwitter" style="color:white;"><span
          class="fa fa-twitter"></span></a>
        <a class="btn btn-social-icon btn-facebook" @click="signInWithFacebook" style="color:white;"><span
          class="fa fa-facebook"></span></a>
        <div class="clearfix"></div>
      </div>
      <p class="text-center or" style="margin-top:5px; margin-bottom:0; padding-bottom:0;">or</p>
      <!-- Tab panes -->
      <div class="tab-content">
        <div class="tab-pane active" id="login" role="tabpanel">
          <div v-if="signInShow" class="alert alert-danger" role="alert">
            {{signInError}}


          </div>
          <div class="form-group label-floating">
            <div class="col-lg-12">

              <input class="form-control" v-model="signInEmail" type="text" placeholder="Enter Email" id="text-input">
            </div>
          </div>
          <div class="form-group" style="margin-top:20px;">
            <div class="col-lg-12">

              <input class="form-control" v-model="signInPassword" type="password" placeholder="Password"
                     id="password-input">
            </div>
          </div>
          <div class="footer-center">
            <div class="login">
              <button type="submit" @click="signIn()" class="btn btn-raised btn-info login-btn"
                      style="width:100%; background-color:#324057;"><b>Login</b></button>
            </div>
          </div>
        </div>
        <div class="tab-pane" id="register" role="tabpanel">
          <div v-if="registerShow" class="alert alert-danger" role="alert">
            {{registerError}}


          </div>
          <div class="form-group">
            <div class="col-lg-12">

              <input class="form-control" autofocus v-model="email" type="text" placeholder="Enter Email"
                     id="email-reg-input">
            </div>
          </div>

          <div class="form-group" style="margin-top:20px;">
            <div class="col-lg-12">
              <input class="form-control" v-model="password" type="password" placeholder="Password"
                     id="password-reg-input">
            </div>
          </div>

          <div class="footer-center">

            <div class="text-center">
              <p style="font-size:12px;">By clicking Sign Up, you are indicating that you have read and agree to the

                <router-link class="term" :to="{ name: getRoute, query: {'modal': 'tou' } }">Terms of Service

                </router-link>
              </p>
            </div>
            <div class="row" style="padding-bottom:10px;">
              <vue-recaptcha ref="recaptcha" @verify="onVerify" :sitekey="key">
              </vue-recaptcha>
            </div>
            <div class="login">
              <button type="submit" @click="signUp()" class="btn btn-raised btn-info login-btn"
                      style="width:100%; background-color:#324057;"><b>Sign Up</b></button>
            </div>
          </div>
        </div>
      </div>

    </div>
    <!-- custom buttons -->
    <div slot="modal-footer" class="footer-center">
      <div class="text-center" v-if="loading" style="margin:0; padding: 0;">
        <clip-loader :loading="loading" :color="'#039BE5'" :size="'35px'"></clip-loader>
      </div>
    </div>

  </modal>
</template>
<style>
  .modal.in {
    background-color: rgba(0, 0, 0, 0.71);
  }

  div.header .modal-content {
    border: none;
    margin-top: 25%;
  }

  button.login-btn {
    padding: 7px 5rem 7px 5rem;
  }

  button.login-btn b {
    font-size: 13px;
  }

  div.footer-center {
    padding: 15px 15px 8px 15px;
    text-align: center !important;
  }

  div.modal-body ul li a {
    margin: 0 !important;
    padding: 5px !important;
    font-size: 12px !important;
  }

  div.modal-body ul li {
    margin: 0;
    padding: 0;
  }

  div.login .nav-tabs .nav-link {
    border: none;
  }

  .footer-center a {
    color: #2196F3 !important;
  }

  div.forgot-pass {
    margin-top: 10px;
    padding-bottom: 10px;
  }

  div.header .modal-footer {
    margin: 0;
    padding: 0;
  }

  p.or {
    font-size: 15px;
    color: #757575;
  }

  div.social-btn a:not(:first-child) {
    margin-left: 6px;
  }

  ul.nav > li.nav-item > a.nav-link.active {
    border-bottom: 2px solid #03a9f4
  }

  ::-webkit-input-placeholder {
    font-size: 13px !important;
    color: #9E9E9E !important;
  }

  :-moz-placeholder { /* Firefox 18- */
    font-size: 13px !important;
    color: #9E9E9E !important;
  }

  ::-moz-placeholder { /* Firefox 19+ */
    font-size: 13px !important;
    color: #9E9E9E !important;
  }

  div.header div.modal-content {
    border-radius: 5px !important;
  }

  div.header .modal-dialog {
    max-width: 350px;
  }

  div.header .modal-header {
    padding: 5px 24px;
    border: none;
    background-color: #1F2D3D;
    border-radius: 5px 5px 0 0 !important;
  }

  div.modal-body {
    padding: 0 !important;
    margin: 0 !important;
    padding-top: 10px !important;
  }

  .nav-tabs .nav-item:focus, .nav-tabs .nav-item a:focus {
    outline: none;
    background: #fff;
    border: none;
  }

</style>
<script>
  import FireBaseService from 'firebase'
  import fireBase from '../../firebase/vueFire'
  import {secondFireBaseApp} from '../../firebase/vueFire'
  import modal from '../Modal/Modal.vue'
  import {Message} from 'element-ui'
  import {MessageBox} from 'element-ui'
  var FacebookProvider = null
  var GoogleProvider = null
  var TwitterProvider = null
  export default{
    name: 'Login',
    props: {
      show: {
        type: Boolean,
        required: true,
        default: false
      },
      setActive: {
        type: String,
        default: 'login'
      }
    },
    data(){
      return {
        key: process.env.RECAPTCHA,
        activeTab: 0,
        reReponse: '',
        justified: true,
        navStyle: 'pills',
        username: '',
        email: '',
        password: '',
        registerError: '',
        registerShow: false,
        signInError: '',
        signInPassword: '',
        signInEmail: '',
        signInShow: false,
        loading: false
      }
    },
    methods: {
      toggleModal: function () {
        this.$emit('toggle')
      },
      backClose: function () {
        this.onCancel()
      },
      onCancel: function () {
        this.$refs.recaptcha.reset()
        this.reResponse = ''
        this.resetFields()
        this.errors.clear()
        this.$emit('onCancel')
      },
      setTab: function (value) {
        this.$emit('setTab', value)
      },
      onVerify (response) {
        this.reResponse = response
      },
      signUp: function () {
        this.$validator.attach('reResponse', 'required', {
          prettyName: 'Captcha verify',
          context: () => this.reResponse,
          getter: (context) => context
        })
        this.$validator.validateAll().then((result) => {
          this.loading = true;
          secondFireBaseApp.auth().createUserWithEmailAndPassword(this.email, this.password).then((result) => {
            result.sendEmailVerification()
            this.loading = false;
            this.registerShow = false;
            this.resetFields()
            this.onCancel()
            MessageBox.alert('Congratulation!, your account has been successfully created. You will need check your email and verify your account before you can use the site.', 'Verification Required!', {
              confirmButtonText: 'OK',
              type: 'success'
            })
          }).catch((error) => {
            this.loading = false;
            this.registerShow = true;
            this.registerError = error.message
          })
        }).catch((error) => {
          this.contactLoading = true
          Message({
            showClose: true,
            message: "Please fill out all the required fields",
            type: 'error'
          })
        })
      },
      resetFields: function () {
        this.email = ''
        this.password = ''
        this.signInEmail = ''
        this.signInPassword = ''
        this.registerShow = false
        this.signInShow = false
      },
      signIn: function () {
        this.loading = true
        fireBase.auth().signInWithEmailAndPassword(this.signInEmail, this.signInPassword)
          .then((response) => {
            this.signInShow = false
            this.loading = false
            this.$root.$refs.toastr.Add({
              title: "", // Toast Title
              msg: "Successfully Logged In!", // Message
              clickClose: false, // Click Close Disable
              timeout: 1000, // Remember defaultTimeout is 5 sec..
              position: "toast-top-center", // Toast Position.
              type: "success" // Toast type
            })
            this.onCancel()
            this.$root.$emit('user:logIn')
          }).catch((error) => {
          this.loading = false
          this.signInShow = true
          this.signInError = error.message
        });
      },
      signInWithFacebook: function () {
        if (!FacebookProvider) {
          FacebookProvider = new FireBaseService.auth.FacebookAuthProvider()
        }
        FacebookProvider.setCustomParameters({
          'display': 'popup'
        })
        FireBaseService.auth().signInWithPopup(FacebookProvider).then((result) => {
          this.$store.dispatch('setProvider', 'facebook')
          this.$root.$refs.toastr.s('Successfully Logged In with FaceBook', 'Success')
          this.onCancel()
          this.$root.$emit('user:logIn')
        }).catch((error) => {
          this.$root.$refs.toastr.Add({
            title: "",
            msg: 'Failed to login with FaceBook' + error,
            clickClose: true,
            timeout: 4000,
            position: "toast-top-right",
            type: "error"
          })
        })
      },
      signInWithGoogle: function () {
        if (!GoogleProvider) {
          GoogleProvider = new FireBaseService.auth.GoogleAuthProvider()
        }
        FireBaseService.auth().signInWithPopup(GoogleProvider).then((result) => {
          this.$store.dispatch('setProvider', 'google')
          this.$root.$refs.toastr.s('Successfully Logged In with Google', 'Success')
          this.onCancel()
          this.$root.$emit('user:logIn')
        }).catch((error) => {
          this.$root.$refs.toastr.Add({
            title: "",
            msg: 'Failed to login with Google' + error,
            clickClose: true,
            timeout: 4000,
            position: "toast-top-right",
            type: "error"
          })
        })
      },
      signInWithTwitter: function () {
        TwitterProvider = new FireBaseService.auth.TwitterAuthProvider()
        FireBaseService.auth().signInWithPopup(TwitterProvider).then((result) => {
          this.$store.dispatch('setProvider', 'twitter')
          this.$root.$refs.toastr.s('Successfully Logged In with Twitter', 'Success')
          this.onCancel()
          this.$root.$emit('user:logIn')
        }).catch((error) => {
          this.$root.$refs.toastr.Add({
            title: "",
            msg: 'Failed to login with Twitter' + error,
            clickClose: true,
            timeout: 4000,
            position: "toast-top-right",
            type: "error"
          })
        })
      }
    },
    mounted () {
      if (this.setActive === 'login') {
        $('.nav-tabs a[href="#login"]').tab('show')
      } else if (this.setActive === 'register') {
        $('.nav-tabs a[href="#register"]').tab('show')
      }

    },
    components: {
      'modal': modal
    },
    computed: {
      getRoute () {
        if (this.$route.query.modal) {
          this.$emit('modal', this.$route.query)
        }
      }
    },
    watch: {
      setActive () {
        if (this.setActive === 'login') {
          $('.nav-tabs a[href="#login"]').tab('show')
        } else if (this.setActive === 'register') {
          $('.nav-tabs a[href="#register"]').tab('show')
        }
      }
    }
  }
</script>



