import Vue from 'vue'
import Router from 'vue-router'
import fireBase from '../firebase/vueFire'
import store from '../vuex/index'
import _ from 'lodash'
import {checkAuth} from '../util/auth-helper'
import VueAnalytics from 'vue-analytics'
Vue.use(Router)
const path = process.env.API_PATH

function RequireAuth(to, from, next) {
  if (!store.getters.isAuth) {
    next({
      path: '/',
      query: {redirect: to.fullPath}
    })
  } else {
    next()
  }
}


function isAuth(to, from, next) {
  var fire = fireBase
  if (checkAuth()) {
    if (!store.getters.isAuth) {
      fire.auth().onAuthStateChanged(function (user) {
        if (user) {
          store.dispatch('setUser', user)
          next()
        } else {
          next()
        }
      });
    } else {
      next()
    }
  } else {
    next()
  }
}

const index = resolve => {
  // require.ensure is Webpack's special syntax for a code-split point.
  require.ensure(['../components/layout/Index'], () => {
    resolve(require('../components/layout/Index'))
  })
}
const AppContent = resolve => {
  require.ensure(['../components/layout/Content'], () => {
    resolve(require('../components/layout/Content'))
  })
}
const AllBargains = resolve => {
  require.ensure(['../components/layout/bargains/layout/all-bargains'], () => {
    resolve(require('../components/layout/bargains/layout/all-bargains'))
  })
}
const Detail = resolve => {
  require.ensure(['../components/layout/Detail'], () => {
    resolve(require('../components/layout/Detail'))
  })
}

const ParentContent = resolve => {
  require.ensure(['../components/layout/content-parent'], () => {
    resolve(require('../components/layout/content-parent'))
  })
}

const verify = resolve => {
  require.ensure(['../components/layout/account-verify/account-verify.vue'], () => {
    resolve(require('../components/layout/account-verify/account-verify.vue'))
  })
}


const User = resolve => {
  require.ensure(['../components/user/user'], () => {
    resolve(require('../components/user/user'))
  })
}

const UserBargains = resolve => {
  require.ensure(['../components/user/user-bargains/user-bargains.vue'], () => {
    resolve(require('../components/user/user-bargains/user-bargains.vue'))
  })
}

const todayBargains = resolve => {
  require.ensure(['../components/layout/bargains/layout/last-24-hour'], () => {
    resolve(require('../components/layout/bargains/layout/last-24-hour'))
  })
}


const bestBargains = resolve => {
  require.ensure(['../components/layout/bargains/layout/popular'], () => {
    resolve(require('../components/layout/bargains/layout/popular'))
  })
}

const last7d = resolve => {
  require.ensure(['../components/layout/bargains/layout/last-7-days'], () => {
    resolve(require('../components/layout/bargains/layout/last-7-days'))
  })
}

const editorPick = resolve => {
  require.ensure(['../components/layout/bargains/layout/editor-picks'], () => {
    resolve(require('../components/layout/bargains/layout/editor-picks'))
  })
}

const bookmark = resolve => {
  require.ensure(['../components/layout/favorites/bookmark.vue'], () => {
    resolve(require('../components/layout/favorites/bookmark.vue'))
  })
}

const likes = resolve => {
  require.ensure(['../components/layout/likes/likes.vue'], () => {
    resolve(require('../components/layout/likes/likes.vue'))
  })
}

const UserProfile = resolve => {
  require.ensure(['../components/user/profile/user-profile.vue'], () => {
    resolve(require('../components/user/profile/user-profile.vue'))
  })
}
const UpdatePhoto = resolve => {
  require.ensure(['../components/user/profile/update-photo/update-photo.vue'], () => {
    resolve(require('../components/user/profile/update-photo/update-photo.vue'))
  })
}
const PasswordReset = resolve => {
  require.ensure(['../components/user/profile/password-reset/password-reset.vue'], () => {
    resolve(require('../components/user/profile/password-reset/password-reset.vue'))
  })
}
const AddBargain = resolve => {
  require.ensure(['../components/user/user-bargains/add-bargain/add-bargain.vue'], () => {
    resolve(require('../components/user/user-bargains/add-bargain/add-bargain.vue'))
  })
}
const EditBargain = resolve => {
  require.ensure(['../components/user/user-bargains/edit-bargain/edit-bargain.vue'], () => {
    resolve(require('../components/user/user-bargains/edit-bargain/edit-bargain.vue'))
  })
}

const StoreDeals = resolve => {
  require.ensure(['../components/store-deals/store-deals.vue'], () => {
    resolve(require('../components/store-deals/store-deals.vue'))
  })
}

const ListDeals = resolve => {
  require.ensure(['../components/store-deals/deal-list/deal-list.vue'], () => {
    resolve(require('../components/store-deals/deal-list/deal-list.vue'))
  })
}

var router =  new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: index,
      meta: 'The Best Daily Bargains and Offers',
      beforeEnter: isAuth,
      children: [
        {
          path: '',
          component: ParentContent,
          meta: 'The Best Daily Bargains and Offers',
          children: [
            {
              path: '',
              component: AppContent,
              meta: 'The Best Daily Bargains and Offers',
              children: [
                {
                  path: '',
                  component: AllBargains,
                  name: 'allBargains',
                  meta: 'The Best Daily Bargains and Offers'
                },
                {
                  path: '/last24h',
                  component: todayBargains,
                  name: 'todayBargains',
                  meta: 'The Best Bargains In The Last 24 hours'
                },
                {
                  path: '/popular',
                  component: bestBargains,
                  name: 'bestBargains',
                  meta: 'Popular Bargains'
                },
                {
                  path: '/last7d',
                  component: last7d,
                  name: 'last7d',
                  meta: 'The Best Bargains In The Last 7 Days'
                },
                {
                  path: '/editors',
                  component: editorPick,
                  name: 'editorPick',
                  meta: 'The Top Editor\'s Picks For Best Bargains',
                },
                {
                  path: '/bookmarked',
                  beforeEnter: RequireAuth,
                  component: bookmark,
                  name: 'bookmark',
                  meta: 'Your Bookmarked Bargains'
                },
                {
                  path: '/liked',
                  beforeEnter: RequireAuth,
                  component: likes,
                  name: 'likes',
                  meta: 'Your Liked Bargains'
                }
              ]
            },
            {
              path: '/detail/:id/:title',
              component: Detail,
              name: 'bargainDetail'
            },
            {
              path: '/offers',
              component: StoreDeals,
              meta: 'The Best Offers and Deals From The Top Popular Stores',
              children: [
                {
                  path: '',
                  component: ListDeals,
                  name: 'ListDeals',
                  meta: 'Store Deals'
                }
              ]
            }
          ]
        },
        {
          path: '/accverify',
          component: verify,
          name: 'accverify',
          meta: 'Verify Bargains Account'
        },
        {
          path: '/user',
          component: User,
          beforeEnter: RequireAuth,
          children: [
            {
              path: '',
              redirect: {name: 'UserBargains'}
            },
            {
              path: 'bargains',
              component: UserBargains,
              name: 'UserBargains',
              meta: 'User Bargains',
              children: [
                {
                  path: 'add',
                  component: AddBargain,
                  name: 'addBargain',
                  meta: 'Add Bargain'
                },
                {
                  path: 'edit',
                  component: EditBargain,
                  name: 'editBargain',
                  meta: 'Edit Bargain'
                }
              ]
            },
            {
              path: 'profile',
              component: UserProfile,
              name: 'UserProfile',
              meta: 'User Profile',
              children: [
                {
                  path: 'update',
                  component: UpdatePhoto,
                  name: 'UpdatePhoto',
                  meta: 'Update Photo'
                },
                {
                  path: 'password',
                  component: PasswordReset,
                  name: 'passwordReset',
                  meta: 'Password Reset'
                }
              ]
            }
          ]
        }
      ]
    }
  ]
})

Vue.use(VueAnalytics, {
  id: process.env.GA,
  router,
  autoTracking: {
    pageviewTemplate: function (route) {
      var title = (route.name !== 'bargainDetail') ? 'Best Bargains - ' + route.meta : route.par
      return {
        path: route.path,
        title: title,
        location: window.location.href
      }
    }
  }
})

router.beforeEach((to, from, next) => {
  if (!store.getters.isEmpty) {
    router.app.$http.get(path + '/stores').then((response) => {
      store.dispatch('setList', response.body)
      store.dispatch('setObj', _.keyBy(response.body, (o) => {
        return o._id
      }))
    }, () => {
    })
  }
  next()
})

router.afterEach((to, from) => {
  if (!_.isEmpty(to.meta)) {
    document.title = 'Best Bargains - ' + to.meta
  }
  if (!store.getters.isEmpty) {
    router.app.$http.get(path + '/stores').then((response) => {
      store.dispatch('setList', response.body)
      store.dispatch('setObj', _.keyBy(response.body, (o) => {
        return o._id
      }))
    }, () => {
    })
  }
})


export default router



